/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track::before,
.slick-track::after {
  display: table;
  content: "";
}

.slick-track::after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* @charset "UTF-8";

// Default Variables

// Slick icon entity codes outputs the following
// "\2190" outputs ascii character "←"
// "\2192" outputs ascii character "→"
// "\2022" outputs ascii character "•" */
:root,
::after,
::before {
  --slick-font-path: "./fonts/";
  --slick-font-family: "carkeek-blocks";
  --slick-arrow-font-size: 45px;
  --slick-loader-path: "./img/";
  --slick-arrow-color: #ccc;
  --slick-dot-color: #ccc;
  --slick-dot-color-active: var(--slick-dot-color);
  --slick-prev-character: "";
  --slick-next-character: "";
  --slick-dot-size: 10px;
  --slick-opacity-default: 0.5;
  --slick-opacity-on-hover: 1;
  --slick-opacity-not-active: 0.75;
  --slick-arrow-pos: 20px;
  --slick-arrow-pos-mobile: 7px;
}

/* Slider */
.slick-list .slick-loading .slick-list {
  background: #fff url(img/ajax-loader.gif) center center no-repeat;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: var(--slick-arrow-font-size);
  width: var(--slick-arrow-font-size);
  line-height: 0px;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 200;
}
.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover::before, .slick-prev:focus::before,
.slick-next:hover::before,
.slick-next:focus::before {
  opacity: var(--slick-opacity-on-hover);
}
.slick-prev.slick-disabled::before,
.slick-next.slick-disabled::before {
  opacity: var(--slick-opacity-not-active);
}
.slick-prev::before,
.slick-next::before {
  font-family: var(--slick-font-family);
  font-size: var(--slick-arrow-font-size);
  line-height: 1;
  color: var(--slick-arrow-color);
  opacity: var(--slick-opacity-default);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: var(--slick-arrow-pos-mobile);
}
.slick-prev [dir=rtl] {
  left: auto;
  right: var(--slick-arrow-pos-mobile);
}
.slick-prev::before {
  content: var(--slick-prev-character);
}
.slick-prev::before [dir=rtl] {
  content: var(--slick-next-character);
}

.slick-next {
  right: var(--slick-arrow-pos-mobile);
}
.slick-next [dir=rtl] {
  left: var(--slick-arrow-pos-mobile);
  right: auto;
}
.slick-next::before {
  content: var(--slick-next-character);
}
.slick-next::before [dir=rtl] {
  content: var(--slick-prev-character);
}

@media (min-width: 1024px) {
  .slick-prev {
    left: var(--slick-arrow-pos);
  }
  .slick-prev [dir=rtl] {
    left: auto;
    right: var(--slick-arrow-pos);
  }
  .slick-next {
    right: var(--slick-arrow-pos);
  }
  .slick-next [dir=rtl] {
    left: var(--slick-arrow-pos);
    right: auto;
  }
}
/* Dots */
.slick-dots {
  position: absolute;
  bottom: 25px;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  padding: 0 50px 0 0;
